import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const router = new VueRouter({
    mode: 'history',
    routes: [
        { 
            path: '/',
            component: () => import('@/pages/HomePage' /* webpackChunkName: "Home" */),
            name: 'home'
        },
        // {
        //     path: '*',
        //     redirect: '/'
        // }
    ]
})


export default router